'use client'

import styles from './body.module.css'

import { useState } from 'react'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { Button } from '@mui/material'

import SelectInput, { Option } from '../../../SelectInput/SelectInput'

const Body = () => {
    const { t } = useTranslation('signs')

    const zodiacSignsList = [
        'aries',
        'taurus',
        'gemini',
        'cancer',
        'leo',
        'virgo',
        'libra',
        'scorpio',
        'sagittarius',
        'capricorn',
        'aquarius',
        'pisces',
    ]

    const zodiacSigns: Option[] = zodiacSignsList.map((sign) => ({
        value: sign,
        label: t(sign),
    }))

    const defaultSign = 'gemini'

    const [signs, setSigns] = useState({
        first: defaultSign,
        second: defaultSign,
    })

    const onChange = (id: string) => (value: string) => {
        setSigns({ ...signs, [id]: value })
    }

    return (
        <div className={styles.root}>
            <SelectInput
                value={signs.first}
                options={zodiacSigns}
                onChange={onChange('first')}
            />
            <SelectInput
                value={signs.second}
                options={zodiacSigns}
                onChange={onChange('second')}
            />
            <div className={styles.buttonBox}>
                <Link to={`details/${signs.first}-${signs.second}`}>
                    <Button
                        size="large"
                        className={styles.button}
                        variant="contained"
                    >
                        Go
                    </Button>
                </Link>
            </div>
        </div>
    )
}

export default Body
