import styles from './header.module.css'

import { useTranslation } from 'react-i18next'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import { Link } from 'react-router-dom'

const Header = ({ title }: { title: string }) => {
    const { t } = useTranslation(['signs', 'common'])
    const [firstSign, secondSign] = title.split('-')

    return (
        <header className={styles.header}>
            <div className={styles.back}>
                <Link to="/">
                    <ArrowBackIosIcon color="action" fontSize="large" />
                </Link>
            </div>
            <h1 className={styles.h1}>{t(firstSign)}</h1>
            <h1 className={styles.h1}>{t('common:and')}</h1>
            <h1 className={styles.h1}>{t(secondSign)}</h1>
        </header>
    )
}

export default Header
