import styles from './details.module.css'
import { useParams } from 'react-router-dom'
import Header from './Header/Header'
import { useTranslation } from 'react-i18next'
import ExpandablePanel from '../../ExpandablePanel/ExpandablePanel'

const Details = () => {
    const { signs } = useParams()
    const { t } = useTranslation(['signs', 'commons'])

    const [firstSign, secondSign] = signs!.split('-')
    const sections = ['love', 'friends', 'family', 'work']

    const sectionsContent = sections.map((section) => ({
        title: t(`common:${section}`),
        content: t(`match.${firstSign}.${secondSign}.${section}`),
    }))

    return (
        <div className={styles.root}>
            <Header title={signs!} />
            <section className={styles.summary}>
                <h1>{t('common:summary')}</h1>
                <section>
                    {t(`match.${firstSign}.${secondSign}.summary`)}
                </section>
            </section>
            <section>
                {sectionsContent.map(({ title, content }) => (
                    <ExpandablePanel title={title} content={content} />
                ))}
            </section>
        </div>
    )
}

export default Details
